  font-size: 1.5rem;
}
.nav {
  box-shadow: 0 4px 12px 3px rgb(0 0 0 / 5%);
  /* box-shadow: 0px 1px 3px 1px; */
  position: fixed;
  width: 100vw;
  z-index: 999;
  top: 0;
}
.d-flex {
  margin: 0.3rem;
