  font-weight: bold;
  transition: all ease 0.2s;
}
.nav h2 {
  transition: all ease-in 0.2s;
}
.nav h2:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
.nav-item:hover {
  -webkit-transform: scale(1.13);
          transform: scale(1.13);
  color: rgb(0, 188, 242);
}
